<template>
  <div class="share-swiper mt-11">
    <!-- swiper  -->
    <swiper ref="list" class="share-swiper-content" :options="swiperOption">
      <!-- swiper slide item  -->
      <swiper-slide
        class="share-swiper-item d-flex jc-center"
        v-for="(item, index) in shareSwiper.imgList"
        :key="index"
      >
        <img :src="item.imgLink" />
      </swiper-slide>
      <!--  -->
      <div class="swiper-pagination pagination-share" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  props: ["shareSwiper"],
  data() {
    return {
      swiperOption: {
        slidesPerView: 2,
        spaceBetween: 20,
        pagination: {
          el: ".pagination-share",
          clickable: true
        },
        observer: true, //swiperswiper
        observeParents: true
      }
    };
  },
  // slide  resize
  mounted() {
    this.changeSlidesPerView();
    window.addEventListener("resize", this.changeSlidesPerView);
  },
  methods: {
    // slide 
    changeSlidesPerView() {
      let swiper = this.$refs.list.swiper;
      if (window.innerWidth < 590) {
        swiper.params.slidesPerView = 1;
      } else if (window.innerWidth == 590) {
        swiper.params.slidesPerView = 1;
      } else {
        swiper.params.slidesPerView = 2;
      }
    }
  },
  components: {}
};
</script>

<style lang="scss">
@import "../../../../../assets/scss/_variable";
// swiper  100% 
.share-swiper {
  display: none;
  width: 100%;
  // swiper 
  .share-swiper-content {
    width: 90%;
    padding: 0 0 2rem 0;
    // swiper item 
    .share-swiper-item {
      img {
        width: 80%;
        height: auto;
      }
    }
    // 
    .pagination-share {
      bottom: 0px;
      // (style scoped)
      .swiper-pagination-bullet {
        width: 0.6rem;
        height: 0.6rem;
        margin: 0 0.625rem !important;
        background: map-get($colors, "primary") !important;
        opacity: 1;
        transform: translate(0px, 0px) scale(1);
        transition: all 0.3s;
        &:hover {
          background: map-get($colors, "black-4") !important;
        }
      }
      // 
      .swiper-pagination-bullet-active {
        transform: translate(0px, 0px) scale(1.3);
        background: map-get($colors, "black-4") !important;
        transition: all 0.3s;
      }
    }
  }
}
// 
@media all and (max-width: 900px) {
  .share-swiper {
    display: block;
  }
}
// slide   
@media all and (max-width: 590px) {
  .share-swiper {
    .share-swiper-content {
      width: 100%;
      .share-swiper-item {
        padding: 0 0 2rem 0;
        img {
          width: 50%;
          height: 30%; // height: auto  ()
        }
      }
    }
  }
}
</style>