<template>
  <!--  -->
  <div class="head-small-menu d-flex">
    <!--  -->
    <a
      class="small-menu-button"
      :class="{ show: isShow }"
      @click="isShow=(isShow === false)?true:false;"
    >
      <!--  -->
      <span></span>
    </a>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false
    };
  }
};
</script>

<style lang="scss" scoped>
//  none
.head-small-menu {
  display: none;
  position: absolute;
  left: 1rem;
  //  
  a.small-menu-button {
    width: 2rem;
    height: 4rem;
    // 
    span {
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 0.0667rem;
      background: rgba(255, 255, 255, 0.75);
      // transform: translateY(0);
      transition: all 0.6s;
      // 
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 0.0667rem;
        background: rgba(255, 255, 255, 0.75);
        transform: translateY(-8px);
        transition: all 0.6s;
      }
      // 
      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 0.0667rem;
        background: rgba(255, 255, 255, 0.75);
        transform: translateY(8px);
        transition: all 0.6s;
      }
    }
  }
}
// ( span a.small-menu-button.show 
//             span  span)
a.small-menu-button.show {
  // 
  span {
    background: transparent;
    // 45
    &::before {
      transform: rotate(45deg);
    }
    // 45
    &::after {
      transform: rotate(-45deg);
    }
  }
}
@media all and (max-width: 1180px) {
  // 
  .head-small-menu {
    display: flex;
  }
}
</style>